.acquaintance-model-0003 {
  &__headline {
    margin-left: rem(50px);
  }
  &__inner {
    display: flex;
    &-wrapper {
      flex: 1 1 auto;
      &-img {
        min-height: 600px;
        box-shadow: inset 0 0 150px $fourth-black;
      }
    }
  }
  &__about {
    margin-left: rem(30px);
    flex: 0 0 40%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &-list {
      margin-bottom: rem(30px);
      li {
        span {
          color: darken($fourth-white, 30);
        }
        &::before {
          background-color: $fourth-white;
        }
      }
    }
  }
}
